
.text{
    
    padding-top: 18%;
    color: #cfcfcf;
    font-size: 100px;
    font-family:  sans-serif;
}
.sub_text{
    font-size: 40px;
    font-family: sans-serif;
}
.all{
    width: 100%;
    background-image: linear-gradient(
            to bottom,
            rgba(19, 19, 19, 0) 0%,
            
            rgba(24, 24, 24, 1) 100%
            
          ), url(data:image/jpeg;base64,);
    background-repeat: no-repeat;
    background-size: cover  ;
    height: 1080px;
    text-align: center;
    height: 965px;
}
.link{
    display: flex;
    justify-content: center;
    margin-top: 50px;
}
li>ul>a>img{
    width: 50px;
    height: 50px;
    border-radius: 100%;
    margin: 0 30px;
    animation-name: mix;
    animation-duration:1s; 
}


@keyframes mix{
    0%{
        margin-right:90px ;
        margin-left:90px ;
    }
    100%{
        margin-right:30px ;
        margin-left:30px ;
    }

}
@media ( max-width: 900px ) {
    li>ul>a>img{
        margin-right:10px ;
        margin-left:10px ;
    }
    .text{
        padding-top: 120px;
    }
    
}